@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&family=Squada+One&display=swap);
/*variables */
:root {
	--accent: #34a4dd;
	--black: #000;
	--container-bg: #111111c4;
	--fw-bold: 600;
	--fw-light: 400;
	--fw-normal: 500;
	--heading-ff: 'Squada One', cursive;
	--main-ff: 'Roboto', sans-serif;
	--white: white;
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

html,
body {
	height: 100vh;
}

body {
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
	background: #111111c4;
	background: var(--container-bg);
	background-image: url("https://images.unsplash.com/photo-1571902943202-507ec2618e8f?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=2168&q=80");
	background-position: center;
	color: white;
	color: var(--white);
	font-family: 'Roboto', sans-serif;
	font-family: var(--main-ff);
	font-size: 1.25rem;
	letter-spacing: 1px;
	margin: 0;
}

button,
input,
select,
a {
	color: inherit;
	display: inline-block;
	font-family: inherit;
	font-size: inherit;
	transition: .25s all;
}

button {
	background-color: #34a4dd;
	background-color: var(--accent);
	border-radius: 60px;
	border: none;
	box-shadow: 1px 3px 5px 2px rgba(0, 0, 0, 0.75);
	color: white;
	font-weight: 600;
	font-weight: var(--fw-bold);
	outline: none;
	padding: .2rem .5rem;
}
button:hover,
button:focus {
	box-shadow: none;
	cursor: pointer;
}

input,
select {
	background: #000;
	background: var(--black);
	border: 1px solid white;
	border-bottom: 2px solid white;
	border-radius: 5px;
	color: white;
	padding: .25em .5em;
}
input::-webkit-input-placeholder {
	color: whitesmoke;
}
input:-ms-input-placeholder {
	color: whitesmoke;
}
input::placeholder,
select {
	color: whitesmoke;
}
input:focus,
select:focus {
	border-bottom: 2px solid #34a4dd;
	border-bottom: 2px solid var(--accent);
	font-style: italic;
	outline: none;
}

a {
	color: var(--offwhite);
	font-weight: 400;
	font-weight: var(--fw-light);
	text-decoration: none;
}
a:hover,
a:focus {
	color: #34a4dd;
	color: var(--accent);
	text-decoration: underline;
}

.add-song-form {
	-webkit-align-items: center;
	        align-items: center;
	display: grid;
    -webkit-flex-grow: 1;
            flex-grow: 1;
	grid-gap: .25em;
	grid-template-areas: 
        "title . artist" 
        "link link link" 
        "SLC . suggestedBy"
        "btn btn btn";
    grid-template-columns: 1fr 0px 1fr;
    max-width: 850px;
    margin: 0;
}
#Title {
	grid-area: title;
}
#Artist {
	grid-area: artist;
}
#Link {
	grid-area: link;
}
#SuggestedBy {
	grid-area: suggestedBy;
}
#SongLengthCode {
	grid-area: SLC;
}
.add-song-btn {
	grid-area: btn;
	justify-self: center;
	margin: .25rem;
}

.quote-title {
    font-weight: var(--fw-bold);
    margin-bottom: 1rem;
}

.quote {
    font-size: 1.25rem;
    font-style: italic;
    font-weight: var(--fw-light);
    padding: 0 1rem;
}
h3 {
    border-bottom: 2px solid var(--accent);
    font-family: var(--heading-ff);
    font-size: 2rem;
    margin: 0 auto .25rem;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
}

h4 {
    font-weight: var(--fw-normal);
    margin-bottom: 1rem;
}
h4 span {
    font-style: italic;
}

.link-p {
    font-weight: var(--fw-bold);
}

.suggested-by {
    margin-top: 1rem;
    text-align: right;
}
.suggested-by span {
    color: var(--accent);
}

.squat-text,
.squat-input-number,
.squat-input-btn {
    margin-bottom: 1rem;
}

.squat-text {
    font-size: 1.5rem;
    font-weight: var(--fw-bold);
}

.squat-input-number{
    border: none;
    border-bottom: 2px solid white;
    text-align: right;
    letter-spacing: 1.5px;
    max-width: 200px;
}

.squat-input-btn{
    display: block;
    margin: auto;
}
.App {
	-webkit-align-content: flex-start;
	        align-content: flex-start;
	display: grid;
	grid-gap: 1em;
	grid-template-columns: auto repeat(2, 2fr) auto;
	grid-template-rows: auto 1fr 2fr auto;
	grid-template-areas: "header header header header"
		". squatInput squatInput ." ". quoteDisplay list . "
		"footer footer footer footer";
	min-height: 100vh;
}

header,
footer {
	-webkit-align-items: center;
	        align-items: center;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-evenly;
	        justify-content: space-evenly;
	text-align: center;
}

header {
	background: var(--black);
	grid-area: header;
	font-family: var(--heading-ff);
	margin-top: 0px;
	padding-bottom: 1em;
}

h1 span {
	color: var(--accent);
	display: block;
	font-size: 4.75rem;
	font-weight: var(--fw-bold);
}

h1 {
	border-bottom: 2px solid var(--accent);
	font-size: 4.5rem;
}

.squat-input-form {
	align-self: flex-end;
	grid-area: squatInput;
	justify-self: center;
}
.quote-display,
.song-list {
	-webkit-align-self: center;
	        align-self: center;
	justify-self: center;
}
.quote-display {
	grid-area: quoteDisplay;
}
.song-list {
	grid-area: list;
}

footer {
	align-self: flex-end;
	background: var(--white);
	grid-area: footer;
	padding: .5em;
}

img {
	border-radius: 50%;
	max-width: 10%;
	height: auto;
	-webkit-flex-grow: 0;
	        flex-grow: 0;
}

.squat-input-form,
.quote-display,
.song-list {
	background: var(--container-bg);
	border: 1px solid var(--white);
	border-radius: 10px;
	padding: 1rem;
	text-align: center;
}

/*mobile (is broken at present)*/
@media (max-width: 500px) {
	.App {
		grid-template-areas: "header header header" ". squatInput."
			". quoteDisplay ." ". list ." "footer footer footer";
		grid-template-columns: auto 1fr auto;
		grid-template-rows: auto repeat(3, 1fr) auto;
    width: 100%;
	}
  img {
    display: none;
  }
}

